body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

img {
  width: 160px;
  height: 134px;
  margin: 32px 19px 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-size: cover;
  width: 160px;
  height: 134px;
  margin: 32px 19px 0;
}

.card__name {
  color: #060b35;
  margin: 36px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  margin-left: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin-top: 24px;
  margin-left: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 18px;
  margin-left: 16px;
  display: inline-block;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #f3f3f3;
}

.card__button-text {
  text-transform: uppercase;
  margin-top: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: inline-block;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin: 16px 16px 0;
  display: flex;
}

.stars__star {
  justify-content: space-between;
  align-items: center;
  width: 96px;
  height: 16px;
  display: flex;
}

.stars__reviews {
  color: #060b35;
  text-align: center;
  margin: 0;
  padding-top: 3px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.stars__star--level :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}
